footer {
    background-color: black;
    width: 100%;
    height: 20vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin: 0;
    position: static;
    bottom: 0;
}

footer p{
    color: white;
}
footer img{
    width: auto;
    height: auto;
}

@media screen and (width <= 768px) {
    footer {
        width: 102vw;
    }
}
